import React from 'react'
import GoogleLogin from 'react-google-login'
import {Link, Redirect, useHistory} from 'react-router-dom'

export default function Login() {
  let history = useHistory()
    
  const clientID = '403949553367-u5mto5tmnqttm68j4n37a4hkr1ssukh7.apps.googleusercontent.com';
  const onFailure = (res) => {
console.table(res)
  }
  const onSuccess= (res) => {
     // const img_url = res.profileObj['imageUrl'];
      const names  = res.profileObj['name']
     
      const email = res.profileObj['email'];
      
      history.push("/passwordsetup",{name:names,email:email})

  }
  return (
        <div>
            <GoogleLogin 
                clientId={clientID}
                buttonText="Login with Google"
                onSuccess={onSuccess}
                onFailure={onFailure}
                cookiePolicy={'single_host_origin'}
                isSignedin={true}
                redirectUri='localhost:3000/passwordsetup'
                
                />
        </div>
    )
}
